import React from "react";
import { useState } from "react";
import Work from "./work/Work";
import './main.scss';
import About1 from "../about/About1";
import Contact from '../contact/Contact';
import Particles from "react-tsparticles";

function Main() {

  const [workonoff, setWorkonoff]=useState('slideshowon');
  const [aboutonoff, setAboutonoff ]=useState('aboutboxoff');
  const [contactonoff, setContactonoff]=useState('contactoff');
  const particlesInit = (main) => {
    console.log(main);

    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
  };

  const particlesLoaded = (container) => {
    console.log(container);
  };
  
  return(
    <><Particles
    id="tsparticles"
    init={particlesInit}
    loaded={particlesLoaded}
    options={{
      background: {
        color: {
          value: "#1d2630",
        },
      },
      fpsLimit: 120,
      interactivity: {
        events: {
          onClick: {
            enable: true,
            mode: "push",
          },
          onHover: {
            enable: true,
            mode: "repulse",
          },
          resize: true,
        },
        modes: {
          bubble: {
            distance: 400,
            duration: 2,
            opacity: 0.8,
            size: 40,
          },
          push: {
            quantity: 4,
          },
          repulse: {
            distance: 200,
            duration: 0.4,
          },
        },
      },
      particles: {
        color: {
          value: "#ffffff",
        },
        links: {
          color: "#ffffff",
          distance: 150,
          enable: true,
          opacity: 0.5,
          width: 1,
        },
        collisions: {
          enable: true,
        },
        move: {
          direction: "none",
          enable: true,
          outMode: "bounce",
          random: false,
          speed: 1,
          straight: false,
        },
        number: {
          density: {
            enable: true,
            area: 800,
          },
          value: 80,
        },
        opacity: {
          value: 0.5,
        },
        shape: {
          type: "circle",
        },
        size: {
          random: true,
          value: 5,
        },
      },
      detectRetina: true,
    }}
  />
    <div className="header_box">
      <div className="header_title">
        KIMMINJUN
      </div>
      <div className="header_index">
        <div className="work"onClick={()=>{
          setWorkonoff('slideshowon');
          setAboutonoff('aboutboxoff');
          setContactonoff('contactoff');
        }}>WORK</div>
        <div className="about" onClick={()=>{
          setWorkonoff('slideshowoff');
          setAboutonoff('aboutboxon');
          setContactonoff('contactoff');
        }}>ABOUT</div>
        <div className="contact" onClick={()=>{
          setWorkonoff('slideshowoff');
          setAboutonoff('aboutboxoff');
          setContactonoff('contacton');
        }}>CONTACT</div>
      </div>
    </div>

    <Work onoff={workonoff}></Work>
    <About1 onoff={aboutonoff}></About1>
    <Contact onoff={contactonoff}></Contact>

    <div className="footer_box">
      <div className="footer_title">
        UI Developer / Frontend Developer
      </div>
      <div className="footer_index">
        <a href="mailto:m6827126@gmail.com" className="email">EMAIL</a>
        <a href="https://www.instagram.com/devel_jun/" className="insta">INSTAGRAM</a>
      </div>
    </div>  
    </> 
  )
}

export default Main;